.title {
    margin-bottom: 30px;
    text-align: center;
    text-transform: uppercase;
    font-size: 20px;
    letter-spacing: 0px;
    color: rgb(0, 0, 0);
    font-weight: bold;
  }
  .container {
    width: 600px;
    background-color: rgb(254, 254, 255);
    display: flex;
    box-shadow: 0 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 20%),
      0px 2px 1px rgb(0 0 0 / 40%);
    flex-direction: column;
    justify-content: center;
    margin-right: auto;
    margin-left: auto;
  }